// QueueSystem.tsx
import React, { useState, useEffect } from "react";
import axios from "axios";
import Title from "@/pages/components/atoms/Title";
import CreateTask from "@/pages/components/CreateTask";
import CurrentTask from "@/pages/components/CurrentTask";
import RemainingTasks from "@/pages/components/RemainingTasks"; // Import the RemainingTasks component
import TotalTasksCard from "@/components/TotalTasksCard"; // Import TotalTasksCard
import TasksListCard from "@/components/TasksListCard"; // Import TasksListCard

const QueueSystem = () => {
  const [tasks, setTasks] = useState([]);
  const [taskName, setTaskName] = useState("");
  const [todayTasks, setTodayTasks] = useState([]);
  const [totalTasks, setTotalTasks] = useState(0);

  // Fetch tasks from the server
  const fetchTasks = async () => {
    try {
      const response = await axios.get(`/api/task`);
      setTasks(response.data); // Assuming the response data is an array of tasks
    } catch (error) {
      console.error("Error fetching tasks:", error);
    }
  };

  // Fetch today's tasks and total count
  const fetchTodayTasks = async () => {
    try {
      const response = await axios.get(`/api/task/today`);
      setTodayTasks(response.data.todayTasks);
      setTotalTasks(response.data.totalTasks);
    } catch (error) {
      console.error("Error fetching today's tasks:", error);
    }
  };

  useEffect(() => {
    fetchTasks();
    fetchTodayTasks(); // Fetch today's tasks and count
  }, []);

  // Mark the current task as completed
  const completeTask = async () => {
    if (tasks.length > 0) {
      const [currentTask, ...remainingTasks] = tasks;
      try {
        await axios.put(`/api/task/${currentTask._id}`, {
          completed: true,
        });
        setTasks(remainingTasks); // Remove the completed task locally
      } catch (error) {
        console.error("Error completing task:", error);
      }
    }
  };

  return (
    <div className="p-8 max-w-md mx-auto bg-white space-y-5">
      <Title />
      {/* Task Creation */}
      <CreateTask
        taskName={taskName}
        setTaskName={setTaskName}
        tasks={tasks}
        setTasks={setTasks}
      />
      {/* Current Task */}
      <div className="mb-6">
        {tasks.length > 0 ? (
          <CurrentTask task={tasks[0]} completeTask={completeTask} />
        ) : (
          <p className="text-gray-500">No tasks</p>
        )}
      </div>
      {/* Today's Total Tasks Card */}
      <TotalTasksCard totalTasks={totalTasks} />
      {/* Today's Tasks List Card */}
      <TasksListCard tasks={todayTasks} />
      {/* Remaining Tasks */}
      <RemainingTasks tasks={tasks} />{" "}
      {/* Pass the tasks to RemainingTasks component */}
    </div>
  );
};

export default QueueSystem;
